<template>
  <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-card>
          <!--  -->
          <el-row>
              <el-col>
                  <el-button type="primary" @click="showAddCateDialog"></el-button>
              </el-col>
          </el-row>
          <!--  -->
            <tree-table class="treeTable" :data="catelist" :columns="columns" :selection-type="false" :expand-type = "false" :show-index="true" index-text="#" border :show-row-hover="false">
                <!--  -->
                <template v-slot:isok="scope">
                    <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: lightgreen;"></i>
                    <i class="el-icon-error" v-else style="color: red;"></i>
                </template>
                <!--  -->
                <template v-slot:order="scope">
                    <el-tag size="mini" v-if="scope.row.cat_level===0"></el-tag>
                    <el-tag type="success" size="mini" v-else-if="scope.row.cat_level===1"></el-tag>
                    <el-tag type="warning" size="mini" v-else></el-tag>
                </template>
                <!--  -->
                <template v-slot:opt="scope">
                    <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.cat_id)"></el-button>
                    <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeCatesById(scope.row.cat_id)"></el-button>
                </template>
            </tree-table>
          <!--  -->
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[3, 4, 5, 6]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
      </el-card>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addCateDialogVisible"
        width="50%" @close="addCateDialogClosed">
        <!--  -->
        <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
            <el-form-item label="" prop="cat_name">
                <el-input v-model="addCateForm.cat_name"></el-input>
            </el-form-item>
            <el-form-item label="">
                <!-- options
                    props -->
                <el-cascader
                v-model="selectedKeys"
                :options="parentCateList"
                :props="{ expandTrigger: 'hover',value: 'cat_id',label: 'cat_name',children: 'children',checkStrictly: true}"
                @change="parentCateChange"
                clearable></el-cascader>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addCateDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addCate"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%" @close="editDialogClosed">
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="80px">
          <el-form-item label="" prop="cat_name">
            <el-input v-model="editForm.cat_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editCateInfo"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
  </div>
</template>

<script>
export default {
    data() {
        return {
            queryInfo: {
                type: 3,
                pagenum: 1,
                pagesize: 5
            },
            //
            catelist: [],
            //
            total: 0,
            //table
            columns:[
                {
                    label: '',
                    prop: 'cat_name'
                },
                {
                    label: '',
                    //
                    type: 'template',
                    //
                    template: 'isok'
                },
                {
                    label: '',
                    //
                    type: 'template',
                    //
                    template: 'order'
                },
                {
                    label: '',
                    //
                    type: 'template',
                    //
                    template: 'opt'
                }
            ],
            //
            addCateDialogVisible: false,
            //
            addCateForm: {
                //
                cat_name: '',
                //Id
                cat_pid: 0,
                //
                cat_level: 0
            },
            //
            addCateFormRules: {
                cat_name: [
                    {required:true,message:'',trigger:'blur'}
                ]
            },
            //
            parentCateList: [],
            //id
            selectedKeys: [],
            //
            editDialogVisible: false,
            //
            editForm: {},
            //
            editFormRules: {cat_name: 
            {required:true,message:'',trigger: 'blur'}}
        }
    },
    created() {
        this.getCateList()
    },
    methods: {
        //
        async getCateList(){
            const {data:res} = await this.$http.get('categories',{params: this.queryInfo})
            if(res.meta.status !==200){
                return this.$message.error('')
            }
            // console.log(res.data);
            this.catelist = res.data.result
            this.total = res.data.total
        },
        //pagesize
        handleSizeChange(newSize){
            this.queryInfo.pagesize = newSize
            this.getCateList()
        },
        //pagenum
        handleCurrentChange(newPage){
            this.queryInfo.pagenum = newPage
            this.getCateList()
        },
        //
        showAddCateDialog(){
            //
            this.getParentCateList()
            //
            this.addCateDialogVisible = true
        },
        //
        async getParentCateList(){
            const {data:res} = await this.$http.get('categories',{params:{type: 2}})
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            // console.log(res.data);
            this.parentCateList = res.data
        },
        //
        parentCateChange(){
            // console.log(this.selectedKeys);
            //selectedKeyslength0
            if(this.selectedKeys.length > 0){
                //id
                this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length-1]
                //
                this.addCateForm.cat_level = this.selectedKeys.length
                return
            } else {
                //id
                this.addCateForm.cat_pid = 0
                //
                this.addCateForm.cat_level = 0     
            }
        },
        addCate(){
            // console.log(this.addCateForm);
            this.$refs.addCateFormRef.validate( async valid => {
                if(!valid) return
                const {data:res} = await this.$http.post('categories',this.addCateForm)
                if(res.meta.status !== 201){
                    return this.$message.error('')
                }
                this.$message.success('')
                this.getCateList()
                this.addCateDialogVisible = false
            })
        },
        //
        addCateDialogClosed(){
            this.$refs.addCateFormRef.resetFields()
            this.selectedKeys = []
            this.addCateForm.cat_level = 0
            this.addCateForm.cat_pid = 0
        },
        //
        async showEditDialog(id){
            const{data:res} = await this.$http.get('categories/'+id)
            if(res.meta.status !==200){
                return this.$message.error('')
            }
            this.editForm = res.data
            this.editDialogVisible = true
        },
        //
        editDialogClosed(){
          this.$refs.editFormRef.resetFields()
        },
        //
        editCateInfo(){
            this.$refs.editFormRef.validate(async valid=>{
            if(!valid) return
            //
            const {data:res} = await this.$http.put('categories/' + this.editForm.cat_id,{
              cat_name: this.editForm.cat_name
            })
            if(res.meta.status !== 200){
              return this.$message.error('')
            }
            this.$message.success('')
            //
            this.editDialogVisible = false
            //
            this.getCateList()
        })
        },
        //id
        async removeCatesById(id) {
          //
          const confirmResult = await this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).catch(error => error)
        if(confirmResult !== 'confirm') {
          return this.$message.info('')
        }
        const {data:res} = await this.$http.delete('categories/' + id)
        if(res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.queryInfo.pagenum = 1
        this.getCateList()
        }
    }

}
</script>

<style lang="less" scoped>
.treeTable{
margin-top: 15px;
}
.el-cascader{
    width: 100%;
}
</style>