<template>
  <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

      <!--  -->
      <el-card>
          <!--  -->
          <el-row>
              <el-col>
                  <el-button type="primary" @click="addDialogVisible = true"></el-button>
              </el-col>
          </el-row>

          <!--  -->
          <el-table :data="rolelist" border stripe>
              <el-table-column type="expand">
                <template v-slot="role">
                  <el-row v-for="(item1,i1) in role.row.children" :key="item1.id" :class="['bdbottom', i1===0 ? 'bdtop' : '','vcenter']">
                    <!--  -->
                    <el-col :span="5" >
                      <el-tag closable @close="removeRightById(role.row,item1.id)">{{item1.authName}}</el-tag>
                      <i class="el-icon-caret-right"></i>
                    </el-col>
                    <!--  -->
                    <el-col :span="19">
                      <el-row v-for="(item2,i2) in item1.children" :key="item2.id" :class="[i2===0 ? '' : 'bdtop', 'vcenter']">
                        <el-col :span="6">
                          <el-tag type="success" closable @close="removeRightById(role.row,item2.id)">{{item2.authName}}</el-tag>
                          <i class="el-icon-caret-right"></i>
                        </el-col>
                        <el-col :span="18">
                          <el-tag type="warning" v-for="(item3,i3) in item2.children" :key="item3.id" closable @close="removeRightById(role.row,item3.id)">{{item3.authName}}</el-tag>
                        </el-col>
                      </el-row>
                    </el-col>
                  </el-row>
                </template>
              </el-table-column>
              <el-table-column type="index" label="#"></el-table-column>
              <el-table-column label="" prop="roleName"></el-table-column>
              <el-table-column label="" prop="roleDesc"></el-table-column>
              <el-table-column label="" width="300">
                  <template v-slot="roles">
                      <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(roles.row.id)"></el-button>
                      <!--  -->
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeRolesById(roles.row.id)"></el-button>
                      <!--  -->
                        <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetRightDialog(roles.row)"></el-button>
                  </template>
              </el-table-column>
          </el-table>
      </el-card>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="30%"
        @close="addDialogClosed">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="addForm.roleDesc" v-on:keyup.enter.native='addRole'></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addRole"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%" @close="editDialogClosed">
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="80px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="editForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="editForm.roleDesc" v-on:keyup.enter.native='editRoleInfo'></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editRoleInfo"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="setRightDialogVisible"
        width="50%" @close="setRightDialogClosed">
        <!--  -->
        <el-tree :data="rightslist" :props="treeProps" show-checkbox node-key='id' default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRightDialogVisible = false"> </el-button>
          <el-button type="primary" @click="allotRights"> </el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
export default {
    data() {
        return {
            rolelist: [],
            //
            addForm: {
                roleName: '',
                roleDesc: ''
            },
            //
            addFormRules: {
            roleName: [
                {required:true,message:'',trigger: 'blur'},
                {min:2,max:10,message:'2-10',trigger:'blur'}
            ],
            roleDesc: [
                {required:false,message:'',trigger: 'blur'}
            ]},
            //
            addDialogVisible: false,
            //
            editDialogVisible: false ,
            //
            editForm: {},
            //
            editFormRules: {
                roleName: [
                {required:true,message:'',trigger: 'blur'},
                {min:2,max:10,message:'2-10',trigger:'blur'}
            ],
            roleDesc: [
                {required:false,message:'',trigger: 'blur'}
            ]
            },
            //
            setRightDialogVisible: false,
            //
            rightslist: {},
            //
            treeProps: {
              label:'authName',
              children:'children'
            },
            //id
            defKeys: [],
            //id
            roleId: ''
        }
    },
    created() {
        this.getRolesList()
    },
    methods: {
        async getRolesList(){
            const {data:res} = await this.$http.get('roles')
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.rolelist = res.data
            // console.log(this.rolelist);
        },
        //
        addDialogClosed(){
          this.$refs.addFormRef.resetFields()
        },
        //
        addRole(){
          this.$refs.addFormRef.validate(async valid=>{
            if(!valid) return
            //
            const {data:res} = await this.$http.post('roles',this.addForm)
            if(res.meta.status !== 201){
              return this.$message.error('')
            }
            this.$message.success('')
            //
            this.addDialogVisible = false
            //
            this.getRolesList()
          })
        },
        //
        async showEditDialog(id) {
          //console.log(id);
          const{data:res} = await this.$http.get('roles/'+id)
          if(res.meta.status !==200){
            return this.$message.error('')
          }
          this.editForm = res.data
          this.editDialogVisible = true
        },
        //
        editDialogClosed(){
          this.$refs.editFormRef.resetFields()
        },
        //
        editRoleInfo(){
          this.$refs.editFormRef.validate(async valid=>{
            if(!valid) return
            //
            const {data:res} = await this.$http.put('roles/' + this.editForm.roleId,{
              roleName:this.editForm.roleName,roleDesc:this.editForm.roleDesc
            })
            if(res.meta.status !== 200){
              return this.$message.error('')
            }
            this.$message.success('')
            //
            this.editDialogVisible = false
            //
            this.getRolesList()
        })
        },
        //id
        async removeRolesById(id) {
          //
          const confirmResult = await this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).catch(error => error)
        if(confirmResult !== 'confirm') {
          return this.$message.info('')
        }
        const {data:res} = await this.$http.delete('roles/' + id)
        if(res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getRolesList()
        },
        //id
        async removeRightById(role,rightId) {
          //
          const confirmResult = await this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).catch(error => error)
        if(confirmResult !== 'confirm') {
          return this.$message.info('')
        }
        const {data:res} = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
        if(res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        role.children = res.data
        },
        //
        async showSetRightDialog(role){
          this.roleId = role.id
          //
          const{data:res} = await this.$http.get('rights/tree')
          if(res.meta.status !== 200){
            return this.$message.error('')
          }
          this.rightslist = res.data
          // console.log(this.rightslist);
          //id
          this.getLeafKeys(role,this.defKeys)
          this.setRightDialogVisible = true
        },
        //id,defKeys
        getLeafKeys(node,arr){
          if(!node.children) {
            return arr.push(node.id)
          }
          node.children.forEach(item => {
            this.getLeafKeys(item,arr)
          })
        },
        //
        setRightDialogClosed(){
          this.defKeys=[]
        },
        //
        async allotRights(){
          const keys = [
            ...this.$refs.treeRef.getCheckedKeys(),
            ...this.$refs.treeRef.getHalfCheckedKeys()
          ]
          // console.log(keys);
          const idStr = keys.join(',')
          const {data:res} = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
          if(res.meta.status !== 200){
            return res.$message.error('')
          }
          this.$message.success('')
          this.getRolesList()
          this.setRightDialogVisible = false
        }
        
    }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>