<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable v-on:keyup.enter.native="getGoodsList" @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage"></el-button>
        </el-col>
      </el-row>
      <!-- table -->
      <el-table :data="goodslist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="goods_name"></el-table-column>
        <el-table-column label="" prop="goods_price" width="95px"></el-table-column>
        <el-table-column label="" prop="goods_weight" width="70px"></el-table-column>
        <el-table-column label="" prop="add_time" width="140px">
          <template v-slot="scope">
            {{scope.row.add_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template v-slot="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.goods_id)"></el-button>
            <!--  -->
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 15, 20]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="total" background>
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed" >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="" prop="goods_cat">
          <el-input v-model="editForm.goods_cat"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_name">
          <el-input v-model="editForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input v-model="editForm.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input v-model="editForm.goods_weight"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input v-model="editForm.goods_number"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editCateInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      //
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      //
      goodslist: [],
      //
      total: 0,
      editDialogVisible: false,
      editForm: {
        
      },
      editFormRules: {
        goods_name: [{
          required: true, message: '', trigger: 'blur'
        }],
        goods_price: [{
          required: true, message: '', trigger: 'blur'
        }],
        goods_weight: [{
          required: true, message: '', trigger: 'blur'
        }],
        goods_number: [{
          required: true, message: '', trigger: 'blur'
        }],
        goods_cat: [{
          required: true, message: '', trigger: 'blur'
        }]
      }
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    //
    async getGoodsList () {
      const { data: res } = await this.$http.get('goods', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    //   console.log(res.data);
      this.goodslist = res.data.goods
      this.total = res.data.total
    },
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getGoodsList()
    },
    async removeById (id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(`goods/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getGoodsList()
    },
    goAddpage () {
      this.$router.push('goods/add')
    },
    async showEditDialog (id) {
      this.editDialogVisible = true
      //
      const { data: res } = await this.$http.get(`goods/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
      console.log(this.editForm);
    },
    //
        editDialogClosed(){
          this.$refs.editFormRef.resetFields()
        },
        //
        editCateInfo(){
            this.$refs.editFormRef.validate(async valid=>{
            if(!valid) return
            //
            const {data:res} = await this.$http.put('goods/' + this.editForm.goods_id,{
              goods_name: this.editForm.goods_name,
              goods_price: this.editForm.goods_price,
              goods_weight: this.editForm.goods_weight,
              goods_number: this.editForm.goods_number,
              goods_cat: this.editForm.goods_cat
            })
            if(res.meta.status !== 200){
              return this.$message.error('')
            }
            this.$message.success('')
            this.editDialogVisible = false
            this.getGoodsList()
        })
        }
  }
}
</script>

