<template>
  <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

      <!--  -->
      <el-card>
          <!--  -->
          <el-row :gutter="10">
              <el-col :span="7">
                <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList" v-on:keyup.enter.native='handleCurrentChange(1)'>
                    <el-button slot="append" icon="el-icon-search" @click="handleCurrentChange(1)"></el-button>
                </el-input>
              </el-col>
              <el-col :span="4">
                  <el-button type="primary" @click="addDialogVisible = true"></el-button>
              </el-col>
          </el-row>
          <!--  -->
          <el-table :data="userlist" border stripe>
              <el-table-column label="#" type="index"></el-table-column>
              <el-table-column label="" prop="username"></el-table-column>
              <el-table-column label="" prop="email"></el-table-column>
              <el-table-column label="" prop="mobile"></el-table-column>
              <el-table-column label="" prop="role_name"></el-table-column>
              <el-table-column label="" >
                  <template v-slot = "users">
                      <el-switch v-model="users.row.mg_state" @change="userStateChanged(users.row)">
                      </el-switch>
                  </template>
              </el-table-column>
              <el-table-column label="" width="200">
                  <template v-slot="users">
                      <!--  -->
                      <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(users.row.id)"></el-button>
                      </el-tooltip>
                      <!--  -->
                      <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUsersById(users.row.id)"></el-button>
                      </el-tooltip>
                      <!--  -->
                      <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                        <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRole(users.row)"></el-button>
                      </el-tooltip>
                  </template>
              </el-table-column>

          </el-table>
          <!--  -->
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 2, 4, 8]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
      </el-card>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="30%"
        @close="addDialogClosed">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%" @close="editDialogClosed">
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
          <el-form-item label="">
            <el-input v-model="editForm.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="editForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="editForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="setRoleDialogVisible"
        width="50%" @close="setRoleDialogClosed">
        <div>
          <p>{{userInfo.username}}</p>
          <p>{{userInfo.role_name}}</p>
          <p>
            <el-select v-model="selectedRoleId" placeholder="">
              <el-option
                v-for="item in rolesList"
                :key="item.id"
                :label="item.roleName"
                :value="item.id">
              </el-option>
            </el-select>
          </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRoleDialogVisible = false"> </el-button>
          <el-button type="primary" @click="saveRoleInfo"> </el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
      if (regEmail.test(value)) {
        // 
        return cb();
      }
      cb(new Error(""));
    };
    //;
    var checkMobile = (rule, value, cb) => {
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
      if (regMobile.test(value)) {
        return cb();
      }
      cb(new Error(""));
    };
    return {
        //
        queryInfo: {
            query: '',
            //
            pagenum: 1,
            //
            pagesize: 2
        },
        userlist: [],
        total: 0,
        //
        addDialogVisible: false,
        //
        addForm: {
          username: '',
          password: '',
          email: '',
          mobile: ''
        },
        //
        addFormRules: {
          username: [
            {required:true,message:'',trigger: 'blur'},
            {min:3,max:10,message:'3-10',trigger:'blur'}
          ],
          password: [
            {required:true,message:'',trigger: 'blur'},
            {min:6,max:15,message:'6-15',trigger:'blur'}
          ],
          email: [
            {required:true,message:'',trigger: 'blur'},
            {
            validator: checkEmail,
            message: "",
            trigger: "blur"
            }
          ],
          mobile: [
            {required:true,message:'',trigger: 'blur'},
            {
            validator: checkMobile,
            message: "",
            trigger: "blur"
            }
          ]
        },
        //
        editDialogVisible: false ,
        //
        editForm: {},
        //
        editFormRules: {
          email: [
            {required:true,message:'',trigger: 'blur'},
            {
            validator: checkEmail,
            message: "",
            trigger: "blur"
            }
          ],
          mobile: [
            {required:true,message:'',trigger: 'blur'},
            {
            validator: checkMobile,
            message: "",
            trigger: "blur"
            }
          ]
        },
        //
        setRoleDialogVisible: false,
        //
        userInfo: {},
        //
        rolesList: [],
        //id
        selectedRoleId:''

    };
  },
  created() {
      this.getUserList()
  },
  methods: {
      async getUserList() {
          const {data: res} = await this.$http.get('users',{ params: this.queryInfo})
          if(res.meta.status !==200) {
              return this.$message.error('')
          }
          this.userlist = res.data.users
          this.total = res.data.total
          // console.log(res);
      },
      //pagesize
      handleSizeChange(newSize){
        //    console.log(newSize);
          this.queryInfo.pagesize = newSize
          this.getUserList()
      },
      //
      handleCurrentChange(newPage) {
        //    console.log(newPage);
          this.queryInfo.pagenum = newPage
          this.getUserList()
      },
      //switch
      async userStateChanged(userinfo){
        //   console.log(userinfo);
          const {data:res} = await this.$http.put(
              `users/${userinfo.id}/state/${userinfo.mg_state}`
          )
          if(res.meta.status !== 200) {
              userinfo.mg_state = !userinfo.mg_state
              return this.$message.error('')
          }
          this.$message.success('')
      },
      //
        addDialogClosed(){
          this.$refs.addFormRef.resetFields()
        },
        //
        addUser(){
          this.$refs.addFormRef.validate(async valid=>{
            if(!valid) return
            //
            const {data:res} = await this.$http.post('users',this.addForm)
            if(res.meta.status !== 201){
              return this.$message.error('')
            }
            this.$message.success('')
            //
            this.addDialogVisible = false
            //
            this.getUserList()
          })
        },
        //
        async showEditDialog(id) {
          // console.log(id);
          const{data:res} = await this.$http.get('users/'+id)
          if(res.meta.status !==200){
            return this.$message.error('')
          }
          this.editForm = res.data
          this.editDialogVisible = true
        },
        //
        editDialogClosed(){
          this.$refs.editFormRef.resetFields()
        },
        //
        editUserInfo(){
          this.$refs.editFormRef.validate(async valid=>{
            if(!valid) return
            //
            const {data:res} = await this.$http.put('users/' + this.editForm.id,{
              email:this.editForm.email,mobile:this.editForm.mobile
            })
            if(res.meta.status !== 200){
              return this.$message.error('')
            }
            this.$message.success('')
            //
            this.editDialogVisible = false
            //
            this.getUserList()
        })
        },
        //id
        async removeUsersById(id) {
          //
          const confirmResult = await this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).catch(error => error)
        if(confirmResult !== 'confirm') {
          return this.$message.info('')
        }
        const {data:res} = await this.$http.delete('users/' + id)
        if(res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.queryInfo.pagenum = 1
        this.getUserList()
        },
        //
        async setRole(userInfo){
          this.userInfo = userInfo
          //
          const {data:res} = await this.$http.get('roles')
          if(res.meta.status !==200){
            return this.$message.error('')
          }
          this.rolesList = res.data
          this.setRoleDialogVisible = true
        },
        //
        async saveRoleInfo(){
          if(!this.selectedRoleId){
            this.$message.error('')
          }
          const {data:res} = await this.$http.put(`users/${this.userInfo.id}/role`,{rid:this.selectedRoleId})
          if(res.meta.status !== 200){
            return this.$message.error('')
          }
          this.$message.success('')
          this.getUserList()
          this.setRoleDialogVisible = false
        },
        //
        setRoleDialogClosed(){
          this.selectedRoleId = ''
          this.userInfo = {}
        }
  }
}

</script>
<style lang='less' scoped>
    
</style>