<template>
  <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

      <!--  -->
      <el-card>
          <el-table :data="rightList" border stripe >
              <el-table-column type="index" label="#"></el-table-column>
              <el-table-column label="" prop="authName"></el-table-column>
              <el-table-column label="" prop="path"></el-table-column>
              <el-table-column label="" prop="level">
                  <template v-slot="rights">
                      <el-tag v-if="rights.row.level === '0'"></el-tag>
                      <el-tag type="success" v-else-if="rights.row.level === '1'"></el-tag>
                      <el-tag type="warning" v-else-if="rights.row.level === '2'"></el-tag>
                  </template>
              </el-table-column>
          </el-table>
      </el-card>
  </div>
</template>

<script>
export default {
    data() {
        return {
            rightList: []
        }
    },
    created() {
        this.getrightList()
    },
    methods: {
        async getrightList() {
            const {data:res} = await this.$http.get('rights/list')
            if(res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.rightList = res.data
            // console.log(this.rightList);
        }
    }
}
</script>

