<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" center show-icon :closable="false">
      </el-alert>
      <!--  -->
      <el-steps :active="activeIndex-0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!-- tab -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" label-position="top">
        <el-tabs v-model="activeIndex" :tab-position="'left'" :before-leave="beforeTabLeave" @tab-click="tabClicked">
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader v-model="addForm.goods_cat" :options="catelist" :props="{ expandTrigger: 'hover',label: 'cat_name',value: 'cat_id',children: 'children' }" @change="handleChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!-- item -->
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="cb" v-for="(cb,i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>

          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!-- actionAPI -->
            <el-upload :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture" :headers="headerObj" :on-success="handleSuccess">
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <!--  -->
            <el-button type="primary" class="addbtn" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" alt="" class="previewImg">
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data () {
    return {
      activeIndex: '0',
      //
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        //
        goods_cat: [],
        //
        pics: [],
        //
        goods_introduce: '',
        attrs: []
      },
      addFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //
      catelist: [],
      //
      manyTableData: [],
      //
      onlyTableData: [],
      //URL
      uploadURL: 'http://47.101.61.134:12345/api/private/v1/upload',
      //
      headerObj: {
        Authorization:
          window.sessionStorage.getItem('token')
      },
      previewPath: '',
      previewVisible: false
    }
  },
  created () {
    this.getCatList()
  },
  methods: {
    //
    async getCatList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.catelist = res.data
      //   console.log(this.catelist);
    },
    //
    handleChange () {
      //   console.log(this.addForm.goods_cat);
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    beforeTabLeave (activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    async tabClicked () {
      //
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(`categories/${this.addForm.goods_cat[2]}/attributes`, {
          params: { sel: 'many' }
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
          //   console.log(item.attr_vals);
        })
        // console.log(res.data);

        this.manyTableData = res.data
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(`categories/${this.addForm.goods_cat[2]}/attributes`, {
          params: { sel: 'only' }
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        // console.log(res.data);

        this.onlyTableData = res.data
        // console.log(this.onlyTableData);
      }
    },
    //
    handlePreview (file) {
      console.log(file);
      this.previewPath = 'http://47.101.61.134:12345/' + file.response.data.url.slice(22)
      this.previewVisible = true
    },
    //
    handleRemove (file) {
      //   console.log(file);
      const filePath = file.response.data.tmp_path
      const i = this.addForm.pics.findIndex(x => {
        x.pic === filePath
      })
      this.addForm.pics.splice(i, 1)
      //   console.log(this.addForm);
    },
    //
    handleSuccess (response) {
      //   console.log(response);
      const picInfo = {
        pic: response.data.tmp_path
      }
      this.addForm.pics.push(picInfo)
      //   console.log(this.addForm);
    },
    //
    add () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        //
        //lodash cloneDeep(obj)
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        //
        this.manyTableData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          }
          this.addForm.attrs.push(newInfo)
        })
        this.onlyTableData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        // console.log(form);
        const { data: res } = await this.$http.post('goods', form)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId () {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}

.previewImg {
  width: 100%;
}
.addbtn {
  margin-top: 15px;
}
</style>